body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
	font-size:2rem
}

.App {
    text-align: center;
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    /* justify-content: center; */
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
}

.main {
    position: relative;
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.inputs {
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin: 18px 0px 18px 0px;
}

#title {
    margin-bottom: 5vh;
}

#submit {
    position: absolute;
    right: 0;
    height: 1.4rem;
}

#thumbnail, #result {
    cursor: pointer;
}

#share {
    height: 1.4rem;
}

input {
    text-align: center;
    height: 1.4rem;
    font-size: 1rem;
}

img {
    max-width: 600px;
    width: 90vw;
    margin: 12px;
}

.width100 {
    width: 100%;
}

.App-header {
    top: 0px;
    max-width: 600px;
    background-color: #282c34;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 1vmin);
    color: white;
    margin-top: 2vh;
}

.App-link {
    color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.p-tooltip {
    /* Used to position the arrow */
    position: relative;
}
.input-tooltip {
	bottom: 30px;
}
.submit-tooltip {
	/* bottom: 30px; */
}

/* Show the arrow and content when hovering the trigger element */
/* .p-tooltip:hover .p-tooltip-arrow,
.p-tooltip:hover .p-tooltip-content {
    opacity: 1;
}
 */
.p-tooltip-arrow {
    /* Invisible by default */
    opacity: 0;

    /* Border */
    border: 8px solid transparent;
    border-top-color: #cc413e;

    /* Position */
    bottom: 100%;
    left: 50%;
    position: absolute;
    -webkit-transform: translate(-50%, 8px);
            transform: translate(-50%, 8px);

    /* Zero size */
    height: 0;
    width: 0;

    /* Displayed on top of other element */
    z-index: 10;
}

.p-tooltip-content {
    /* Invisible by default */
    opacity: 0;

    /* Background color, must be the same as the border color of arrow */
    background-color: #cc413e;
    border-radius: 2px;

    /* Position */
    bottom: 100%;
    left: 50%;
    position: absolute;
    -webkit-transform: translate(-50%, -8px);
            transform: translate(-50%, -8px);

    /* Displayed on top of other element */
    z-index: 10;
	
	/* 정렬 및 크기 */

    display: flex;
    justify-content: center;
    align-items: center;
    width: 200px;
    height: 28px;
}
